Handlebars.registerPartial("page/rules/layout", this["tippliga-rebuild"]["page/rules/layout"] = Handlebars.template({"compiler":[7,">= 4.0.0"],"main":function(container,depth0,helpers,partials,data) {
    return "<h1>REGELWERK</h1>\n<br />\n\n<table>\n    <tr>\n        <td class=\"knopf\">1</td>\n        <td class=\"regeln-text\">\n            <b>Jedes Spiel darf nur EINMAL getippt werden. Also aufpassen beim Tippen (wir werden keine \u2028Änderungen nachpflegen und auch versäumte Tipps nicht nachtragen !!!)\n            </b>\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">2</td>\n        <td class=\"regeln-text\">\n            Jede/r Spieler/in repräsentiert genau eine Erst-, Zweit-, bzw. Drittligamannschaft, z. B.:<br /><br />\n            - SW Werder Bremen Fötchen<br 7>\n            - Schalke 04 Jörn<br />\n            - Kaiserslautern Uwe<br />\n            usw...\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">3</td>\n        <td class=\"regeln-text\">\n            Jede/r Spieler/in (1., 2. und 3. Liga) tippt alle 9 Spiele des aktuellen <b>Erstliga</b>spieltages.<br /><br /><br />\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">4</td>\n        <td class=\"regeln-text\">\n            Punkte für richtig getippte Spiele werden wie folgt vergeben:<br />\n            - Richtiger Ergebnistipp 5 Punkte<br />\n            - Richtige Tendenz + Tordifferenz (auch bei Unentschieden) 4 Punkte<br />\n            - Richtige Tendenz 3 Punkte\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">5</td>\n        <td class=\"regeln-text\">\n            Die Spieler tippen immer paarweise gegeneinander an einem Spieltag und zwar genau\u2028so wie die Erst-, Zweit- bzw.\n            Drittligapaarung lautet. D. h. am 1. Spieltag spielt zum Beispiel Werder gegen Schalke und somit tippt Fötchen also\n            gegen Jörn. Angenommen Fötchen erwirtschaftet mit seinen Tipps 18 Punkte und Jörn 10 Punkte, so geht diese\n            \u2028Tippligapartie 18:10 aus. Werder (Fötchen) bekommt wie in der Bundesliga 3 Punkte\u2028 dafür. Außerdem\n            hat er jetzt eine \"Tordifferenz\" von 18:10 (Tippdifferenz). Jörn geht \u2028leer aus und hat 0 Punkte\n            für diesen Spieltag und 10:18 \"Tordifferenz\". Alle anderen \u2028Spieler treten ebenso in ihrem Tippduell\n            an wie diese beiden. \u2028Am 2. Spieltag geht es genau so weiter. Wieder tippen alle Spieler die 9 Spiele. \u2028\n            Diesmal tippt Uwe gegen Fötchen, weil der SV Werder bei Kaiserslautern antritt (s. o.).\n            Bei Unentschieden gibt es wie in der Bundesliga für beide Mannschaften (Tipper/innen) \u20281 Punkt.\n            Aus den Ergebnissen der Spieltage werden nach jedem Spieltag die aktuellen \u2028Tabellen ermittelt.\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">6</td>\n        <td class=\"regeln-text\">\n            Die Drittligabegegnungen in der Tippliga haben im Gegensatz zur 1. und 2. Liga nichts mit dem realen Spielplan zu tun, es gibt in der 3. Tippliga auch nur 18 Mannschaften, dass war technisch nicht anders zu regeln. Ist ja aber auch wurstegal, oder?\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">7</td>\n        <td class=\"regeln-text\">\n            Die Tipps für den aktuellen Spieltag müssen immer bis spätesten zum \u2028Spieltagsbeginn online abgegeben werden. Sollte ein/e Spieler/in keinen Tipp abgegeben \u2028haben, wird der Tipp mit 0 Punkten gewertet. \u2028Die gesamten aktuellen Spieltagtipps der Tippliga können nach Tippabgabeschluss online eingesehen werden.\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">8</td>\n        <td class=\"regeln-text\">\n            Notorisches Nichttippen aus Vergesslichkeit oder taktischen Erwägungen wird bestraft. Dreimal in der Saison nicht getippt bedeutet die Gelbe Karte, die Rote Karte folgt nach zwei weiteren nichtgetippten Spieltagen und zieht den Ausschluss aus der Tippliga nach sich! <b>Wobei der endgültige Ausschluss vom Sportgericht entschieden wird!</b>\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">9</td>\n        <td class=\"regeln-text\">\n            Der Spieleinsatz beträgt 0 € ;)\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">10</td>\n        <td class=\"regeln-text\">\n            Die Mannschaften die am Ende der Saison in der 1. Liga auf Platz 16, 17, und 18 \u2028stehen steigen in die 2. Liga ab, entsprechend steigen die ersten drei Mannschaften\u2028der 2. Liga auf. Gleiches gilt natürlich auch für die anderen Ligen\n        </td>\n    </tr>\n    <tr>\n        <td class=\"knopf\">11</td>\n        <td class=\"regeln-text\">\n            <b>Jedes Spiel darf nur EINMAL getippt werden. Also aufpassen beim Tippen (wir werden keine \u2028Änderungen nachpflegen und auch versäumte Tipps nicht nachtragen !!!) </b>\n        </td>\n    </tr>\n\n</table>";
},"useData":true}));

